let data
	const loginUser = async (e) => {
		e.preventDefault()
		setIsLoading(true)
		
		const response = await fetch(`${url}/api/token/`, {
			method: 'POST',
			headers: {
				'Content-Type':'application/json'
			},
			body: JSON.stringify({'username':e.target.username.value,  'password': e.target.password.value})

		})
		data = await response.json()
		console.log('data:', data)
		console.log('response:', response)
		if(response.status === 200) {
			setAuthTokens(data)
			setUser(jwt_decode(data.access))
			const random = jwt_decode(data.access)
			console.log("RANDOM", random.id)
			const randomID = random.id
			localStorage.setItem('random', randomID)
			setIsLoggedIn(true)
			localStorage.setItem('authTokens', JSON.stringify(data))
			setIsLoading(false)

			setTimeout(async() => {
						var requestOptions = {
						  method: 'GET',
						  redirect: 'follow'
						};

						const random = localStorage.getItem('random')

						const res = await fetch(`${url}/users/profile/${random}`, requestOptions)
						const data = await res.json()
						console.log("LOGIN PROFILE DATA", data)

						if (res.status == 200) {
							if (data.is_customer === true) {
							console.log(data.is_customer)
							localStorage.setItem('status', false)
							navigate('/')
							setIsLoading(false)
							setIsCustomerLoggedIn(true)
								dispatch({
						            type: SNACKBAR_OPEN,
						            open: true,
						            message: 'Customer Login Successful',
						            variant: 'alert',
						            alertSeverity: 'success'
						        });
						} else if(data.is_staff === true){
							console.log(data.is_vendor)
							localStorage.setItem('status', true)
							navigate('/dashboard/analytics')
							setIsLoading(false)
								dispatch({
						            type: SNACKBAR_OPEN,
						            open: true,
						            message: 'Staff Login Successful',
						            variant: 'alert',
						            alertSeverity: 'success'
						        });

						}else{
							console.log(data.is_vendor)
							localStorage.setItem('status', true)
							navigate('/dashboard/default')
							setIsLoading(false)
								dispatch({
						            type: SNACKBAR_OPEN,
						            open: true,
						            message: 'Vendor Login Successful',
						            variant: 'alert',
						            alertSeverity: 'success'
						        });
						}
						}

					}, 10)

			dispatch({
			            type: SNACKBAR_OPEN,
			            open: true,
			            message: 'Login Successful',
			            variant: 'alert',
			            alertSeverity: 'success'
			        });

		}else {
			setIsLoading(false)
			dispatch({
			            type: SNACKBAR_OPEN,
			            open: true,
			            message: 'Login Failed',
			            variant: 'alert',
			            alertSeverity: 'error'
			        });
		}
	}


