import * as actionTypes from './actions';

export const initialState = {
	customers: [
		{name: 'Ben Nyakaz', email: 'ben@gmail.com', location: 'Harare, Zimbabwe', order: 150, date: '18.10.2021', status: 1},
		{name: 'Jane Doe', email: 'jane@gmail.com', location: 'Mutare, Zimbabwe', order: 250, date: '25.12.2021', status: 2},
		{name: 'John Doe', email: 'john@gmail.com', location: 'Bulawayo, Zimbabwe', order: 120, date: '10.06.2021', status: 1}
	]
}


const customerReducer = (state = initialState, action) => {
	switch (action.type) {
		case actionTypes.GET_CUSTOMERS:
			return {
				...state
			}
		default:
			return state;
	}
};

export default customerReducer;
