import React, { lazy } from 'react';
import { useRoutes } from 'react-router-dom';

// project imports
import MainRoutes from './MainRoutes';
import LoginRoutes from './LoginRoutes';
import AuthenticationRoutes from './AuthenticationRoutes';
import Loadable from 'ui-component/Loadable';

const PagesLanding = Loadable(lazy(() => import('views/pages/landing')));
const AppECommCheckout = Loadable(lazy(() => import('views/application/e-commerce/Checkout')));
const AppOrderTracker = Loadable(lazy(() => import('views/application/e-commerce/OrderTracker')));
const AppProductService = Loadable(lazy(() => import('views/pages/productService')));
const AppECommProductDetails = Loadable(lazy(() => import('views/application/e-commerce/ProductDetails')));

// ===========================|| ROUTING RENDER ||=========================== //

export default function ThemeRoutes() {
return useRoutes([{ path: '/', element: <PagesLanding /> }, { path: '/heckout', element: <AppECommCheckout /> } , {path: '/e-commerce/productdetails/:id', element: <AppECommProductDetails/>}, { path: '/ordertracker', element: <AppOrderTracker /> }, { path: '/productService', element: <AppProductService/> },AuthenticationRoutes, LoginRoutes, MainRoutes]);
}