import React from 'react';

// material-ui
import { Typography } from '@material-ui/core';


import useAuth from 'hooks/useAuth'

// project imports
import NavGroup from './NavGroup';
import menuItem from 'menu-items';

// ===========================|| SIDEBAR MENU LIST ||=========================== //

const MenuList = () => {

    const {user} = useAuth()
    let navItems

    if (user.is_staff === true) {
    navItems = menuItem.items.filter(item => item.is_for_staff === true)
    .map((item) => {
        switch (item.type) {
            case 'group':
                return <NavGroup key={item.id} item={item} />;
            default:
                return (
                    <Typography key={item.id} variant="h6" color="error" align="center">
                        Menu Items Error
                    </Typography>
                );
        }
    });
} else if(user.is_staff === false ) {
    navItems = menuItem.items.filter(item => item.is_for_staff === false || item.is_for_all === true)
    .map((item) => {
        switch (item.type) {
            case 'group':
                return <NavGroup key={item.id} item={item} />;
            default:
                return (
                    <Typography key={item.id} variant="h6" color="error" align="center">
                        Menu Items Error
                    </Typography>
                );
        }
    });
}

    return navItems;
};

export default MenuList;
