import * as actionTypes from './actions';
//import { v4 as uuidv4 } from 'uuid';

export const initialState = {
	products: [
		{id: '0001' , name: 'Apple Watch', category: 'Smart Watch', price: '400', date: '12.07.2018', qty: '45', desc: '', barcode: '', SKU: '', discount: '', brand: '', weight: '', extra_shipping_fee: '', tags: '', product_images: ''},
		{id: '0002' , name: 'Samsung S21', category: 'Smart Phone', price: '999', date: '12.07.2018', qty: '30'},
		{id: '0003' , name: 'Tesla Model Y', category: 'Automobile', price: '40000', date: '12.07.2018', qty: '5'},
		{id: '0004' , name: 'Razer Blade 15', category: 'Laptop', price: '1800', date: '12.07.2018', qty: '15'},
		{id: '0005' , name: 'M1X Macbook Pro', category: 'Laptop', price: '1500', date: '12.07.2018', qty: '10'},
	]
}


const productReducer = (state = initialState, action) => {
	switch (action.type) {
		case actionTypes.GET_PRODUCTS:
			return {
				...state
			};
		case actionTypes.DELETE_PRODUCT:
			console.log('Here From Product Reducer')
			return {
				...state,
				products: state.products.filter(product => product.id !== action.payload )
			};
		case actionTypes.ADD_PRODUCT:
			console.log('Here From Product Reducer')
			return {
				...state,
				products: [action.payload, ...state.products]
			};
		default:
			return state;
	}
};

export default productReducer;
